"use client";
import fetcher from "@/utils/fetcher";
import { Badge } from "@workspace/ui/components/badge";
import { Button } from "@workspace/ui/components/button";
import {
	Card,
	CardContent,
	CardFooter,
	CardHeader,
	CardTitle,
} from "@workspace/ui/components/card";
import { Input } from "@workspace/ui/components/input";
import {
	ArrowLeft,
	ArrowLeftRight,
	ArrowRight,
	ExternalLink,
	Loader2,
} from "lucide-react";
import { useSearchParams } from "next/navigation";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import useSWR from "swr";
import { z } from "zod";

const searchSchema = z.string().min(1);

const useSearch = (query: string) => {
	const { data, error, isLoading } = useSWR(
		query ? `/api/search?query=${encodeURIComponent(query)}` : null,
		fetcher,
	);
	return { data, error, isLoading };
};

export default function SearchPage() {
	const [query, setQuery] = useState("");
	const router = useRouter();
	const searchParams = useSearchParams();

	useEffect(() => {
		const queryParam = searchParams.get("query");
		if (queryParam) {
			setQuery(queryParam);
		}
	}, [searchParams]);

	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		const result = searchSchema.safeParse(query);
		if (!result.success) {
			return;
		}
		router.push(`/search?query=${encodeURIComponent(query)}`);
	};

	// Get current query parameter
	const currentQuery = searchParams.get("query") || "";

	// Fetch search results
	const { data, error, isLoading } = useSearch(currentQuery);

	// Format date to be more readable
	const formatDate = (dateString: string) => {
		const date = new Date(dateString);
		return date.toLocaleDateString("en-US", {
			year: "numeric",
			month: "short",
			day: "numeric",
		});
	};

	// Extract arXiv ID from the full URL
	const getArxivId = (url: string) => {
		const matches = url.match(/\/abs\/([^\/]+)$/);
		return matches ? matches[1] : url;
	};

	// Function to truncate summary text
	const truncateSummary = (text: string, maxLength = 250) => {
		if (text.length <= maxLength) return text;
		return text.slice(0, maxLength) + "...";
	};

	return (
		<main className="flex flex-col items-center px-4 max-w-7xl mx-auto">
			<div className="flex flex-col items-center text-center max-w-3xl mb-8 w-full">
				{/* Search Bar */}
				<form
					onSubmit={handleSubmit}
					className="flex items-center gap-2 border rounded-full p-1 w-full max-w-2xl mt-8 shadow-sm"
				>
					<Input
						value={query}
						onChange={(e) => setQuery(e.target.value)}
						placeholder="Search..."
						className="border-none shadow-none focus-visible:ring-0"
					/>
					<Button
						size="icon"
						className="rounded-full cursor-pointer"
						type="submit"
					>
						<ArrowRight />
					</Button>
				</form>
			</div>

			{/* Search Results */}
			<div className="w-full max-w-4xl">
				{currentQuery && (
					<div className="mb-4">
						<h2 className="text-xl font-medium">
							{isLoading
								? "Searching..."
								: error
									? "Error fetching results"
									: data &&
										`${data.metadata.totalResults} results for "${currentQuery}"`}
						</h2>
					</div>
				)}

				{/* Loading State */}
				{isLoading && (
					<div className="flex justify-center items-center py-12">
						<Loader2 className="h-8 w-8 animate-spin text-primary" />
						<span className="ml-3 text-lg">Searching arXiv papers...</span>
					</div>
				)}

				{/* Error State */}
				{error && (
					<div className="rounded-md bg-red-50 p-4 my-4">
						<div className="flex">
							<div>
								<h3 className="text-sm font-medium text-red-800">
									Error fetching search results
								</h3>
								<div className="mt-2 text-sm text-red-700">
									Please try again later or modify your search query.
								</div>
							</div>
						</div>
					</div>
				)}

				{/* Results List */}
				{data && data.data && data.data.length > 0 ? (
					<div className="flex flex-col gap-4 mb-8">
						{data.data.map((paper) => (
							<Card key={paper.id} className="overflow-hidden">
								<CardHeader className="pb-2">
									<CardTitle className="text-lg font-medium">
										<a
											href={paper.id}
											target="_blank"
											rel="noopener noreferrer"
											className="hover:underline flex items-start"
										>
											{paper.title}
											<ExternalLink className="ml-2 h-4 w-4 flex-shrink-0" />
										</a>
									</CardTitle>
								</CardHeader>
								<CardContent className="pb-2 text-sm">
									<div className="flex gap-2 items-center mb-3 text-muted-foreground">
										<span>Published: {formatDate(paper.published)}</span>
										<span>•</span>
										<span>arXiv: {getArxivId(paper.id)}</span>
									</div>
									<div className="mb-3">
										<div className="flex flex-wrap gap-1 mb-2">
											{paper.author.map((author, idx) => (
												<Badge
													key={idx}
													variant="secondary"
													className="font-normal"
												>
													{author.name}
												</Badge>
											))}
										</div>
									</div>
									<p className="text-muted-foreground">
										{truncateSummary(paper.summary)}
									</p>
								</CardContent>
								<CardFooter className="pt-2">
									<a
										href={paper.id}
										target="_blank"
										rel="noopener noreferrer"
										className="text-primary text-sm hover:underline"
									>
										View on arXiv
									</a>
								</CardFooter>
							</Card>
						))}
					</div>
				) : (
					// No results state
					data &&
					data.data &&
					data.data.length === 0 && (
						<div className="text-center py-12">
							<h3 className="text-lg font-medium">No results found</h3>
							<p className="text-muted-foreground mt-2">
								Try adjusting your search query or explore different topics.
							</p>
						</div>
					)
				)}

				{/* Pagination (Simple version) */}
				{data && data.data && data.data.length > 0 && (
					<div className="flex justify-between items-center my-8">
						<Button
							variant="outline"
							size="sm"
							onClick={() => {
								const currentStart = Number(searchParams.get("start") || 0);
								const newStart = Math.max(0, currentStart - 20);
								router.push(
									`/search?query=${encodeURIComponent(currentQuery)}&start=${newStart}`,
								);
							}}
							disabled={Number(data.metadata.start) <= 0}
							className="flex items-center gap-1"
						>
							<ArrowLeft className="h-4 w-4" /> Previous
						</Button>

						<span className="text-sm text-muted-foreground">
							Showing {Number(data.metadata.start) + 1} -{" "}
							{Math.min(
								Number(data.metadata.start) +
									Number(data.metadata.itemsPerPage),
								Number(data.metadata.totalResults),
							)}{" "}
							of {data.metadata.totalResults}
						</span>

						<Button
							variant="outline"
							size="sm"
							onClick={() => {
								const currentStart = Number(searchParams.get("start") || 0);
								const newStart = currentStart + 20;
								router.push(
									`/search?query=${encodeURIComponent(currentQuery)}&start=${newStart}`,
								);
							}}
							disabled={
								Number(data.metadata.start) +
									Number(data.metadata.itemsPerPage) >=
								Number(data.metadata.totalResults)
							}
							className="flex items-center gap-1"
						>
							Next <ArrowRight className="h-4 w-4" />
						</Button>
					</div>
				)}
			</div>
		</main>
	);
}
